"use client";
import { useEffect, useState } from "react";

export default function Contratar() {
  const [open, setOpen] = useState(true);
  const [method, setMethod] = useState<"cartao" | "pix">("cartao");

  useEffect(() => {
    setOpen(true);
  }, []);

  const inputBase =
    "w-full rounded-2xl border border-gray-200 bg-white px-4 py-2 text-gray-900 placeholder:text-gray-400 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-orange-500";

  return (
    <main className="min-h-screen py-16">
      <div className="max-w-6xl mx-auto px-4">
        <h1 className="text-4xl md:text-5xl font-bold text-gray-900">Contratar Plano</h1>
        <p className="mt-3 text-gray-600">Preencha seus dados para concluir o pagamento.</p>
      </div>

      {open && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
          <div className="absolute inset-0 bg-black/50 backdrop-blur-sm" onClick={() => setOpen(false)} />
          <div
            role="dialog"
            aria-modal="true"
            aria-labelledby="modal-title"
            className="surface rounded-default relative z-10 w-full max-w-2xl p-6"
          >
            <div className="flex items-start justify-between">
              <div>
                <h2 id="modal-title" className="text-2xl font-bold text-gray-900">Pagamento</h2>
                <p className="mt-1 text-sm text-gray-600">Insira seus dados para confirmar a contratação.</p>
              </div>
              <button
                type="button"
                onClick={() => setOpen(false)}
                className="rounded-2xl px-3 py-2 text-gray-700 hover:bg-gray-100 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-orange-500"
                aria-label="Fechar"
              >
                ✕
              </button>
            </div>

            <form className="mt-6 grid gap-4" onSubmit={(e) => e.preventDefault()}>
              <div className="grid md:grid-cols-2 gap-4">
                <div>
                  <label className="text-sm font-medium text-gray-700">Nome completo</label>
                  <input className={inputBase} type="text" name="nome" placeholder="Seu nome" required />
                </div>
                <div>
                  <label className="text-sm font-medium text-gray-700">Email</label>
                  <input className={inputBase} type="email" name="email" placeholder="seu@email.com" required />
                </div>
              </div>
              <div className="grid md:grid-cols-2 gap-4">
                <div>
                  <label className="text-sm font-medium text-gray-700">WhatsApp</label>
                  <input className={inputBase} type="tel" name="whatsapp" placeholder="(11) 90000-0000" />
                </div>
                <div>
                  <label className="text-sm font-medium text-gray-700">Método de pagamento</label>
                  <div className="mt-2 flex gap-2">
                    <button
                      type="button"
                      onClick={() => setMethod("cartao")}
                      className={`rounded-2xl px-4 py-2 transition ${
                        method === "cartao" ? "bg-orange-600 text-white" : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                      }`}
                    >
                      Cartão
                    </button>
                    <button
                      type="button"
                      onClick={() => setMethod("pix")}
                      className={`rounded-2xl px-4 py-2 transition ${
                        method === "pix" ? "bg-orange-600 text-white" : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                      }`}
                    >
                      Pix
                    </button>
                  </div>
                </div>
              </div>

              {method === "cartao" && (
                <div className="grid md:grid-cols-2 gap-4">
                  <div>
                    <label className="text-sm font-medium text-gray-700">Número do cartão</label>
                    <input className={inputBase} type="text" name="cardNumber" placeholder="0000 0000 0000 0000" />
                  </div>
                  <div>
                    <label className="text-sm font-medium text-gray-700">Nome no cartão</label>
                    <input className={inputBase} type="text" name="cardName" placeholder="Como está no cartão" />
                  </div>
                  <div>
                    <label className="text-sm font-medium text-gray-700">Validade</label>
                    <input className={inputBase} type="text" name="expiry" placeholder="MM/AA" />
                  </div>
                  <div>
                    <label className="text-sm font-medium text-gray-700">CVV</label>
                    <input className={inputBase} type="text" name="cvv" placeholder="123" />
                  </div>
                </div>
              )}

              {method === "pix" && (
                <div className="surface rounded-default p-4">
                  <div className="text-sm text-gray-700">Pix será gerado após confirmar os dados.</div>
                </div>
              )}

              <div className="mt-2 flex items-center justify-between">
                <button
                  type="button"
                  onClick={() => setOpen(false)}
                  className="rounded-2xl px-4 py-2 bg-gray-100 text-gray-700 hover:bg-gray-200 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-orange-500"
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  className="rounded-2xl px-6 py-3 bg-orange-600 text-white font-semibold hover:bg-orange-700 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-orange-500"
                >
                  Continuar
                </button>
              </div>
              <div className="mt-3 text-center text-sm text-gray-500">Valor: R$ 149,90</div>
            </form>
          </div>
        </div>
      )}
    </main>
  );
}